$f_primary: 'Roboto';
body{
  font-family: $f_primary !important;
}
.header{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background: #2C2307;
  padding: 11.5px 110px;
  z-index: 99999;
  .desc_linl_pnt{
    margin: 0 auto;
  }
  .header_inn{
    align-items: center;
  }
  .link_prnt{
    display: flex;
    align-items: center;
    a{
      font-family: $f_primary;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      color: #FFFFFF;
      background: transparent;
      box-shadow: none;
      margin-right:55px;
      transition: all 0.2s ease-out;
      cursor: pointer;
      &:hover{
        color: #F3BA2F;
      }
      &.active{
        color: #F3BA2F;
      }
    }
    .last_link{
      margin-right: 0;
    }
  }
  .enterapp_prnt{
    .enterapp_btn{
      width: 147px;
      height: 42px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid #F3BA2F;
      background: #F3BA2F;
      border-radius: 60px;
      font-weight: 700;
      font-size: 14px;
      line-height: 16px;
      text-align: center;
      color: #353535;
      border: 1px solid #F3BA2F;
      transition: all 0.2s ease-out;
      &:hover{
        background: #000000;
        color: #ffffff;
      }
    }
  }
}
@media screen and (max-width: 1600px) {
  .header {
    padding: 10px 60px;
  }
  .header .link_prnt a {
    margin-right: 30px;
  }
}
@media screen and (max-width: 1199px) {
  .header {
    padding: 10px 30px;
  }
  .header .link_prnt a {
    margin-right: 15px;
    font-size: 13px;
  }
}
@media screen and (min-width: 992px) {
