.upcro_launch_main{
  display: flex;
  max-width: 1390px;
  margin: 0 auto;
  width: 100%;
  padding-top: 140px;
  padding-right: 15px;
  padding-left: 15px;
  .launch_text_box{
    min-height: 693px;
    display: flex;
    align-items: center;
    width: 100%;
    .launch_text_box_inn{
      max-width: 640px;
      margin-left: auto;
      padding-right: 7px;
      h3{
        font-family: $f_primary;
        font-weight: 600;
        font-size: 46px;
        line-height: 54px;
        color: #FFFFFF;
      }
      p{
        font-family: $f_primary;
        font-weight: 400;
        font-size: 22px;
        line-height: 34px;
        color: #FFFFFF;
        margin-top: 32px;
        margin-bottom: 55px;
      }
      .launch_btns{
        display: flex;
        align-items: center;
        a{
          border: 1px solid #F3BA2F;
          background: #F3BA2F;
          border-radius: 60px;
          width: 200px;
          height: 42px;
          font-family: $f_primary;
          font-weight: 700;
          font-size: 14px;
          line-height: 16px;
          text-align: center;
          color: #353535;
          position: relative;
          &:focus{
            box-shadow: none;
          }
          &:hover{
            background: #000000;
            color: #ffffff;
          }
        }
        a:before {
          content: '';
          background: linear-gradient(45deg, #413005, #926e12, #bb8e1b, #ffcc49, #ffb52c, #eb8b0e, #e48d45, #dd632a, #d82b00);
          position: absolute;
          top: -4px;
          left:-4px;
          background-size: 400%;
          z-index: -1;
          filter: blur(5px);
          width: calc(100% + 8px);
          height: calc(100% + 8px);
          animation: glowing 20s linear infinite;
          opacity: 0;
          transition: opacity .3s ease-in-out;
          border-radius: 60px;
        }
        a:active {
          color: #000
        }
        a:active:after {
            background: transparent;
        }
        a:hover:before {
            opacity: 1;
        }
        a:after {
            z-index: -1;
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            background: #111;
            left: 0;
            top: 0;
            border-radius: 60px;
        }
        @keyframes glowing {
          0% { background-position: 0 0; }
          50% { background-position: 400% 0; }
          100% { background-position: 0 0; }
        }
        .entr_a{
          margin: 0 15px;
        }
      }
    }
  }
  .radial_gradiant_box{
    max-width: 691px;
    height: 691px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: radial-gradient(50.83% 50.39% at 50.98% 49.9%, #493A03 0%, #14110b00 100%);
  }
}
.width_full{
  width: 100%; 
}
