          font-size: 44px;
          line-height: 52px;
          color: #FFFFFF;
        }
        p{
          font-family: $f_primary;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          color: #FFFFFF;
          margin-top: 28px;
          margin-bottom: 38px;
        }
        a{
          font-family: $f_primary;
          font-weight: 700;
          font-size: 14px;
          line-height: 16px;
          color: #FFFFFF;
          background: #12151A;
          border-radius: 60px;
          width: 247px;
          height: 42px;
          border: 1px solid #F3BA2F;
          position: relative;
          box-shadow: none;
          transition: all 0.2s ease-out;
          &:hover{
            color: #F3BA2F;
          }
          a:before {
            content: '';
            background: linear-gradient(45deg, #413005, #926e12, #bb8e1b, #ffcc49, #ffb52c, #eb8b0e, #e48d45, #dd632a, #d82b00);
            position: absolute;
            top: -4px;
            left:-4px;
            background-size: 400%;
            z-index: -1;
            filter: blur(5px);
            width: calc(100% + 8px);
            height: calc(100% + 8px);
            animation: glowing 20s linear infinite;
            opacity: 0;
            transition: opacity .3s ease-in-out;
            border-radius: 60px;
          }
          a:active {
            color: #000
          }
          a:active:after {
              background: transparent;
          }
          a:hover:before {
              opacity: 1;
          }
          a:after {
              z-index: -1;
              content: '';
              position: absolute;
              width: 100%;
              height: 100%;
              background: #111;
              left: 0;
              top: 0;
              border-radius: 60px;
          }
          @keyframes glowing {
            0% { background-position: 0 0; }
            50% { background-position: 400% 0; }
            100% { background-position: 0 0; }
          }
        }
      }
    }
