.pulse {
  
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0px 15px 80px rgba(243, 186, 47, 0.5);
  animation: pulse 2s infinite;
}
.pulse:hover {
  animation: none;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0px 15px 80px rgba(243, 186, 47, 0.5);
  }
  70% {
      -webkit-box-shadow: 0px 15px 80px rgba(243, 186, 47, 0);
  }
  100% {
      -webkit-box-shadow: 0px 15px 80px rgba(243, 186, 47, 0.5);
  }
}
@keyframes pulse {
  0% {
    -moz-box-shadow: 0px 15px 80px rgba(243, 186, 47, 0.5);
    box-shadow: 0px 15px 80px rgba(243, 186, 47, 0.5);
  }
  70% {
      -moz-box-shadow: 0px 15px 80px rgba(243, 186, 47, 0);
      box-shadow: 0px 15px 80px rgba(243, 186, 47, 0);
  }
  100% {
      -moz-box-shadow: 0px 15px 80px rgba(243, 186, 47, 0.5);
      box-shadow: 0px 15px 80px rgba(243, 186, 47, 0.5);
  }
}
